.gnb {
  width: 100%;
  height: 40px;
  display: flex;
  border-bottom: 1px solid #bcbcbc;
  .hidden {
    width: 53%;
    height: 40px;
    border-right: 1px solid #bcbcbc;
  }
  .search_wrap {
    width: 340px;
    padding: 8px;
    border-right: 1px solid #bcbcbc;
    #search {
      border: 2px solid #000;
      border-radius: 3px;
      width: 220px;
      height: 19px;
      margin-left: 10px;
    }
    button {
      border: none;
      background-color: #fff;
      font-size: 12px;
      text-transform: uppercase;
      padding-left: 30px;
    }
  }
  ul {
    display: flex;
    .left {
      padding-left: 40px;
    }
    li {
      padding: 10px;
      padding-left: 20px;
      font-size: 13px;
      width: 90px;
      .compareImg {
        display: inline-block;
        background: url(../../images/compare.png) no-repeat;
        background-size: cover;
        width: 14px;
        height: 14px;
        &:hover {
          background: url(../../images/compare_hover.png) no-repeat;
          background-size: cover;
          width: 14px;
          height: 14px;
        }
        a {
          display: inline-block;
          width: 60px;
          margin-left: 18px;
        }
      }
      .cartImg {
        display: inline-block;
        background: url(../../images/cart.png) no-repeat;
        background-size: cover;
        width: 14px;
        height: 14px;
        margin-left: 20px;
        &:hover {
          background: url(../../images/cart_hover.png) no-repeat;
          background-size: cover;
          width: 14px;
          height: 14px;
        }
        a {
          display: inline-block;
          width: 60px;
          margin-left: 22px;
        }
      }
      a {
        text-decoration: none;
      }
      &:hover {
        color: blue;
      }
    }
  }
}
